import React from "react";
import DescriptionIcon from "@material-ui/icons/Description";
import Button from "@material-ui/core/Button";
const btn4 =  {"backgroundColor": "#007bff",
"color": "#fff",
"padding": "4px 50px",
"textTransform": "capitalize",
/* width: 200px; */
"border": "1px solid #eee",
"borderRadius":"3px",
"font-size": "13px"}

const Documents = () => {


  return (
    <div className="container-fluid">
      <div className="row page-title">
        <div className="col-md-12">
          <nav aria-label="breadcrumb" className="float-right mt-1">
            <ol className="breadcrumb">
              <li className="breadcrumb-item">
                <a href="/">IPolicyMart</a>
              </li>
              <li className="breadcrumb-item">
                <a href="/users-document">Document</a>
              </li>
            </ol>
          </nav>
          <h3 className="mb-1 mt-0">
            <b>Document</b>
          </h3>
          <p className="sub-header">
            Here, you can upload document to help you in policy issuance
          </p>
        </div>
      </div>
      <div className="row justify-content-center">
        <div className="col-lg-6">
          <div style={{ minHeight: "300px" }} className="card card-with-shadow">
            <div style={{ textAlign: "center" }} className="card-body ">
              <div style={{ height: "40px" }} />
              <DescriptionIcon
                style={{ fontSize: "80px", color: "dodgerblue" }}
              />
              <div style={{ height: "40px" }} />
              <p className="sub-header">Kepp your Documents organized</p>
              <div style={{ height: "20px" }} />
            
                {/* <button className="btn4">
                 
                  + Add Document
                  <input type="file" hidden onChange={()=>{}} />
                  </button> */}
               <Button style={{...btn4}} component="label">
               + Add Document (pdf-only)
            <input type='file' accept="application/pdf,application/vnd.ms-excel" hidden onChange={()=>{}} />
          </Button>

              <p className="sub-header"></p>
            </div>{" "}
            {/* end card-body*/}
          </div>{" "}
          {/* end card*/}
        </div>{" "}
      </div>
    </div>
  );
};

export default Documents;
